import React, { Component } from 'react'
import Myswiper from '../../../common/myswiper/myswiper'
import {NavLink} from "react-router-dom"

export default class coming extends Component {
   constructor(props){
     super(props);
     this.state = {
        banner: props.banner
     }

   }

   toClass(){
     this.props.history.push("/class");
   }

   render(){
     let {banner} = this.state;
     let options = {
       autoplay:true,
       loop:true
     };
     return(
      <div id="banner">
     
       <Myswiper pagination options={options}>
           { banner.map(item => (
  
           <div className="swiper-slide" key={item.id}>
             <NavLink to="/class">
             <img src={item.pic}  alt="这是课程"/>
             <p className="wellcom">
                 <span>进入班级</span>
             </p>
             </NavLink>
          </div>))
  
          }
        </Myswiper>
     </div>
 
     )
   }

}
