import React, { Component } from 'react'

const doNothing = ()=>{}

const getVal = function(cb,ev){
   if(ev.keyCode === 13){
    let val = ev.target.value.trim();
    val && cb(ev.target.value);
  }
}

const getItem = function(cb,ev){
   let val = ev.target.innerText.trim();
   val && cb(val);
}


const KeyList = (props)=>(
  <ul id="keyList">
    {
      props.showList.length>0 && props.showList.map((item,index)=>(
        (item.tt_name||item.label_name)&&(
        <li key={index} 
        className="border-bottom textflow"
        onClick={props.searchAction}>
          <span className="iconfont"></span>
          <span>{item.tt_name+"-"+item.label_name}</span>
        </li>
      )))
    }
  </ul>
)

export default class header extends Component {

  constructor(props){
     super(props);
     this.state = {
       showList:[]
     }
  }

  setShowList(rs){
    rs&&this.setState({
      showList:rs.items
    });
  }

  render() {
    let {showList} = this.state;
    return (
      <header className="header">
       <div className="top">
         <span className="iconfont icon-tubiaozhizuomoban fdj"></span>

         <input placeholder="搜索你要的课程..." className="search" 
         onFocus={this.props.focusAction?this.props.focusAction:doNothing}
         onKeyDown={this.props.searchAction?getVal.bind(this,this.props.searchAction):doNothing}
         onInput={(ev)=>{
          this.props.showList(ev.target.value,this.setShowList.bind(this));
        }}/>

         <KeyList 
         showList={showList?showList:[]}
         searchAction={this.props.searchAction?getItem.bind(this,this.props.searchAction):doNothing}/>

         {this.props.sys &&<span className="iconfont icon-saoyisao sys
  "></span>}
        {!this.props.sys && <span className="cancel" onClick={this.props.cancelAction}>取消</span>}
       </div>
    </header>
    )
  }
}