import React, { Component } from 'react'
import Myswiper from '../../../common/myswiper/myswiper'
import {Link} from "react-router-dom"


const Con = ()=>{
    return(
       [1,2,3].map((item,index)=>(
       <Link to="/textbook" key={index}>    
       <div key={item} className="itembox">
        <img src="//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLBGOS47DsaYA9MlKW9pZRYTy92Ric1ZowUHNPDzlyyhNEyrmWBcqjyAicu00ALqsD2FM/356?tp=webp"  alt="这是课程"/>
        <div className="right">
            <p className="catoName">集训班</p>
            <p>
                <span className="tabs">零基础</span>
                <span className="tabs">双线学习</span>
            </p>
            <div className="bto">
              <p className="start textflow">
                <span className="iconfont icon-xingxing"></span>
                <span className="iconfont icon-xingxing"></span>
                <span className="iconfont icon-xingxing"></span>
                <span className="iconfont icon-xingxing"></span>
                <span className="iconfont icon-xingxing"></span>
             </p>
             <p className="watch textflow">
                 <span className="iconfont icon-dianzan"></span>
                 <span> 9995</span>
             </p>
           </div>
        </div>
     </div>
     </Link>
    ))
  )
}

export default class hotting extends Component {
    render() {
        return (
            <div className="hotting">
                <h3>热门课程</h3>
                <Myswiper pagination>
                <div className="swiper-slide">
                    <Con />
                </div>
                <div className="swiper-slide">
                    <Con />
                </div>
                </Myswiper>
          </div>
        )
    }
}
