import React, { Component } from 'react'
import Myswiper from '../../../common/myswiper/myswiper'


const Con = ()=>(
    <>
    {[1,2,3,4,5].map(item=>(
     <div className="swiper-slide" 
     style={{width:"138px"}} key={item} >
        <img src="//10.url.cn/qqcourse_logo_ng/ajNVdqHZLLAz2X5AynPIjNZOYN0MdlKq7iagomFKsO8qjPmxcpMibcgPQbmOrD0sJic2ib3eUujzabE/356?tp=webp"  alt="图片"/>
        <p>
           <span className="tabs">国家引进</span>
           <span className="tabs">500强认可</span>
        </p>
        <p>
          <span className="tabs more">了解更多</span>
        </p>
     </div>
     ))}
   </>
)

export default class certificate extends Component {

    render() {
        return (
          <div className="certificate">
            <h3>证书认证</h3>
            <Myswiper>
              <Con />
            </Myswiper>
          </div>
        )
    }
}
