import React, { Component } from 'react'


export default class examine extends Component {
    render() {
        return (
            <div className="examine">
                <h3>考学头条</h3>
                {
        [1,2,3,4,5].map(item=>(<li className="item border-bottom" key={item}>
        <img src="http://10.url.cn/qqcourse_logo_ng/ajNVdqHZLLCsYRGLYibZKJ0PRVQnlokP2hwMia2m9qViakBwmPWiaQsZO0jnDrWyxxJkP50A1xiaic7eE/356?tp=webp" alt="图片"/>
        <div className="right">
            <p>
                <span>报名倒计时</span>
                <span className="tabs">原创</span>
            </p>
            <p>
               文教你报名不踩雷，顺利取证!
            </p>
            <p className="textflow">
                <span className="iconfont icon-dianzan"></span>
                <span className="num">9999</span>
            </p>
        </div>
     </li>))}
            </div>
        )
    }
}
